@import './../../../styles/definitions/type';

:host {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: var(--tp-color-neutral-2);
}

.tp-alert-fixed {
  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
  }
}

::slotted([slot='icon']) {
  min-width: 16px;
  margin-right: 16px;
}

::slotted([slot='text']) {
  color: var(--tp-color-neutral-10);
  text-align: center;
}

//
// color
//
:host(.tp-color) {
  background: var(--tp-color-base);

  ::slotted([slot='icon']) {
    color: var(--tp-color-contrast);
  }

  ::slotted([slot='text']) {
    color: var(--tp-color-contrast) !important;
  }
}

:host(.tp-color-warning),
:host(.tp-color-provas-4) {
  ::slotted([slot='icon']) {
    color: var(--tp-color-neutral-10);
  }

  ::slotted([slot='text']) {
    color: var(--tp-color-neutral-10) !important;
  }
}
